import orderListItem from './order-list-item.vue';
import commonButton from '../common/common-button.vue';
export default {
	components: {
		orderListItem,
		commonButton
	},
	props: {
		item: Object,
		index:Number
	},
	methods:{
		// 去支付
		openPayMethods(){
			// 跳转到支付页面
			uni.navigateTo({
				url: '../pay-methods/pay-methods?detail='+JSON.stringify({
					id:this.item.id,
					price:this.item.total_price
				})
			});
		},
		// 取消订单
		closeOrder(){
			uni.showModal({
				content: '是否要取消该订单？',
				success: (res)=>{
					if (res.confirm) {
						uni.showLoading({
							title: '取消订单中...',
							mask: false
						});
						this.$H.post('/closeorder/'+this.item.id,{},{
							token:true
						}).then(res=>{
							uni.hideLoading()
							uni.showToast({
								title: '取消订单成功',
								icon: 'none'
							});
							this.$emit('update')
						}).catch(err=>{
							uni.hideLoading()
						})
					}
				}
			});
		},
		// 申请退款
		applyRefund(){
			uni.navigateTo({
				url: '/pages/order-refund/order-refund?detail='+JSON.stringify({
					id:this.item.id
				})
			});
		},
		// 查看物流
		openLogistics(){
			uni.navigateTo({
				url: '/pages/logistics-detail/logistics-detail?detail='+JSON.stringify({
					id:this.item.id
				})
			});
		},
		// 确认收货
		received(){
			uni.showModal({
				content: '是否要确认收货？',
				success:  (res)=> {
					if (res.confirm) {
						uni.showLoading({
							title: '确认收货中...',
							mask: false
						});
						this.$H.post('/order/'+this.item.id+'/received',{},{
							token:true
						}).then(res=>{
							uni.hideLoading()
							uni.showToast({
								title: '确认收货成功',
								icon: 'none'
							});
							this.$emit('update')
						}).catch(err=>{
							uni.hideLoading()
						})
					}
				}
			});
		},
		openDetail(){
			uni.navigateTo({
				url: '/pages/order-detail/order-detail?id='+this.item.id,
			});
		},
	}
}
